import React, {Component} from 'react';
import Toolbar from "../components/Toolbar/Toolbar";
import "../index.scss";
import "./styles/index.scss"
import Header from "../components/Header/Header";
import About from "../components/About/About";
import EnterEmail from "../components/EnterEmail/EnterEmail";
import Team from "../components/Team/Team";
import StepsComponent from "../components/Steps/StepsComponent";
import PHONE_ICON from "../assets/icons/phone-24px.svg"
import SUBMIT_ICON from "../assets/icons/submit-24px.svg"
import PEOPLE_ICON from "../assets/icons/people-24px.svg"
import AIRPLANE_ICON from "../assets/icons/airplane-24px.svg"
import LAPTOP_ICON from "../assets/icons/laptop-24px.svg"
import SEARCH_ICON from "../assets/icons/search.svg"
import ContactUs from "../components/ContactUs/ContactUs";
import {animateScroll as scroll, Events as scrollEvents, scrollSpy} from 'react-scroll';
import Sidenav from "../components/Sidenav/Sidenav";
import Footer from "../components/Footer/Footer";


class Index extends Component {

    state = {email: ""};

    steps = [{
        icon: SEARCH_ICON,
        translateKey: "StepsSearch",
        title: "Find Your Programs",
        description: "We offer many different programs in many different countries. Using our booking platform, you can easily search through them all and find the program that fits your unique needs."
    }, {
        icon: SUBMIT_ICON,
        translateKey: "StepsSubmit",
        title: "Submit Applications",
        description: "Using our booking platform, you can easily submit applications for as many programs as you like.  When you submit them, you can rest easily knowing all your personal data is well protected and will only be shared with whom you choose!"
    }, {
        icon: PEOPLE_ICON,
        translateKey: "",
        title: "Interview Preparation",
        description: "After being accepted by the university, you will have one last step before you can attend the program.  In order to receive the Visa for the host country, you must interview. We will help prepare you for this so you do not have to worry!"
    }, {
        icon: PHONE_ICON,
        translateKey: "",
        title: "Talk with us",
        description: "When you talk with us, we will go over all the different programs and locations we have to offer.  You will be able to select from over 20 different programs in over 5 countries.  We will collect all the information required for the program and Visa."
    }, {
        icon: AIRPLANE_ICON,
        translateKey: "",
        title: "Pack your bags",
        description: "Last step you must take before your program begins is to PACK YOUR BAGS!  Next your off on to your experience of a lifetime trip!"
    }];

    componentDidMount() {
        scrollEvents.scrollEvent.register('begin', function (to, element) {
        });

        scrollEvents.scrollEvent.register('end', function (to, element) {
        });

        scrollSpy.update();
    }

    componentWillUnmount() {
        scrollEvents.scrollEvent.remove('begin');
        scrollEvents.scrollEvent.remove('end');
    }

    emailOnchange = (event) => {
        this.setState({email: event.target.value})
    };

    scrollTo = (goTo) => {
        let go = goTo;
        if (isNaN(goTo)) {
            let el = document.getElementById(goTo);
            go = el.offsetTop - 100;
        }

        scroll.scrollTo(go);
    };

    render() {

        const {email} = this.state;

        return (
            <div id="Index">
                <Toolbar scrollTo={this.scrollTo}/>

                <Sidenav scrollTo={this.scrollTo}/>

                <Header scrollTo={this.scrollTo}/>

                <EnterEmail onChange={this.emailOnchange} value={email}/>

                <About/>

                <Team/>

                <StepsComponent steps={this.steps}/>

                <ContactUs/>

                <Footer/>

            </div>);
    }
}

export default Index;